@import "breakpoints";
@import "colors";
@import "fonts";
@import "resets";

:root {
	--default-inline-padding: 1em;

	--transition-speed-normal: 500ms;
}

.btn-solid,
.btn-hollow {
	text-decoration: none;
	cursor: pointer;
	border-radius: 0.9em;
	display: inline-block;
}

.btn-solid {
	background-color: var(--clr-primary-title);
	color: var(--clr-body);
	padding: 0.9em 1.6em;
	border: 1px solid var(--clr-primary-title);
	transition: background-color var(--transition-speed-normal) ease,
		color var(--transition-speed-normal) ease,
		border var(--transition-speed-normal) ease;

	&:hover {
		background-color: transparent;
		color: var(--clr-primary-title);
		border: 1px solid var(--clr-primary-title);
	}

	@include mediaQuery(med) {
		font-size: var(--fs-200);
		padding: 0.8em 2em;
	}

	@include mediaQuery(lrg) {
		font-size: var(--fs-300);
	}
}

.btn-hollow {
	padding: 0.9em 1.6em;
	width: 100%;
	border: 1px solid var(--clr-primary-title);

	transition: background-color var(--transition-speed-normal) ease,
		color var(--transition-speed-normal) ease;

	&:hover {
		background-color: var(--clr-primary-title);
		color: var(--clr-body);
	}

	@include mediaQuery(med) {
		font-size: var(--fs-200);
		padding: 0.8em 2em;
	}

	@include mediaQuery(lrg) {
		font-size: var(--fs-300);
	}
}

.with-arrow {
	position: relative;

	&::after {
		content: url("../images/icon-arrow-down.svg");
		position: absolute;
		right: -16px;
		top: 50%;
		display: inline-block;
		transform: translateY(-50%) rotate(0deg);
		transition: transform var(--transition-speed-normal) ease;
	}

	&.active::after {
		transform: translateY(-50%) rotate(180deg);
	}
}

body {
	background-color: var(--clr-body);
	color: var(--clr-primary);
	font-family: var(--ff-primary);
	font-weight: 400;
}

.nav {
	display: flex;
	justify-content: space-between;
	align-items: center;

	padding: 1.3em var(--default-inline-padding);

	&--desktop {
		display: none;

		@include mediaQuery(med) {
			display: block;
		}
	}
}

.hamburger-container {
	@include mediaQuery(med) {
		display: none;
	}
}

.hamburger__menu {
	padding: 1.3em var(--default-inline-padding);
	background-color: var(--clr-body);
	height: 100%;
	width: 65%;

	position: absolute;
	top: 0;
	right: 0;

	display: flex;
	flex-direction: column;
	gap: 2em;

	opacity: 0;
	pointer-events: none;

	transition: opacity var(--transition-speed-normal) ease;

	&-list {
		list-style: none;
		padding: 0;
		margin: 0;
		margin-block-start: 4em;

		display: flex;
		flex-direction: column;
		gap: 0.8em;

		& a {
			text-decoration: none;
			color: var(--clr-primary);
			transition: color var(--transition-speed-normal) ease;

			&:hover {
				color: var(--clr-primary-title);
			}
		}

		& a.active {
			transform: translateY(-50%) rotate(180deg);
		}

		&-inner {
			list-style: none;
			margin: 0em;
			padding: 1.3em;

			display: flex;
			flex-direction: column;
			gap: 0.8em;

			overflow: hidden;
			max-height: 0;
			opacity: 0;
			pointer-events: none;
			position: absolute;

			transition: max-height var(--transition-speed-normal) ease,
				opacity var(--transition-speed-normal) ease,
				position var(--transition-speed-normal) ease;
		}
	}

	&-item {
		&-inner {
			display: flex;
			justify-content: flex-start;
			align-items: center;
			gap: 1em;
		}
	}

	.with-arrow.active ~ .hamburger__menu-list-inner {
		max-height: 500px;
		opacity: 1;
		pointer-events: auto;
		position: relative;
	}

	&-login-container {
		& ul {
			list-style: none;
			padding: 0;
			margin: 0;
			text-align: center;

			display: flex;
			flex-direction: column;
			gap: 0.8em;
		}

		& a {
			text-decoration: none;
			color: var(--clr-primary);
			font-size: var(--fs-200);
		}
	}
}

.hamburger__icon {
	margin-left: auto;
	cursor: pointer;
	z-index: 99;
	position: relative;

	display: grid;
	grid-template-columns: 1fr;
	place-items: center;

	& > * {
		grid-column: 1 / 2;
		grid-row: 1 / 3;
	}
}

.hamburger__icon.active ~ .hamburger__menu {
	opacity: 1;
	pointer-events: auto;
}

.hamburger__icon .hamburger__icon-open {
	opacity: 1;
	pointer-events: auto;
}

.hamburger__icon.active .hamburger__icon-open {
	opacity: 0;
	pointer-events: none;
}

.hamburger__icon .hamburger__icon-close {
	opacity: 0;
	pointer-events: none;
}

.hamburger__icon.active .hamburger__icon-close {
	opacity: 1;
	pointer-events: auto;
}

.hero {
	@include mediaQuery(med) {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		align-items: center;
		justify-content: space-between;
		grid-auto-columns: 1fr;
		height: 70vh;
		width: 80%;
		max-width: 1200px;
		margin-inline: auto;
	}

	@include mediaQuery(lrg) {
		width: 95%;
		height: 100vh;
	}

	&__img {
		@include mediaQuery(med) {
			order: 2;
		}

		img[mobile] {
			display: block;
			width: 100%;
			object-fit: cover;

			@include mediaQuery(med) {
				display: none;
			}
		}

		img[desktop] {
			display: none;

			@include mediaQuery(med) {
				display: block;
				width: 100%;
				max-width: 600px;
				object-fit: cover;
			}
		}
	}

	&__content {
		@include mediaQuery(med) {
			display: flex;
			justify-content: center;
		}
		&-container {
			text-align: center;
			margin-block-start: 2.3em;
			padding-inline: var(--default-inline-padding);

			display: flex;
			flex-direction: column;
			gap: 1em;

			@include mediaQuery(med) {
				text-align: start;
				margin-block-start: 0;
				padding-inline: 0;
				width: 90%;
				max-width: 400px;
			}

			@include mediaQuery(lrg) {
				max-width: 450px;
			}
		}
	}

	&__title {
		color: var(--clr-primary-title);
		font-size: var(--fs-700);

		@include mediaQuery(med) {
			line-height: 1.1;
			font-size: var(--fs-700);
		}

		@include mediaQuery(lrg) {
			line-height: 1;
			font-size: var(--fs-900);
		}
	}

	&__desc {
		line-height: 1.8;
		@include mediaQuery(med) {
			font-size: var(--fs-200);
		}

		@include mediaQuery(lrg) {
			font-size: var(--fs-300);
		}
	}

	&__icons {
		margin-block: 1.3em;
	}

	&__icons-list {
		padding: 0;
		margin: 0;
		list-style: none;
		display: flex;
		gap: 2em;
		justify-content: space-between;
		align-items: center;
	}
}
